.alloy-panel {
}

.alloy-header {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
}

.alloy-footer {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
  right: 0;
}

.alloy-wrapper {
  position: absolute;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.alloy-scroller {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  user-select: none;
  text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

.alloy-refresh {
  position: absolute;
  top: -40px;
  left: 0;
  right: 0;
  display: block;
  overflow: hidden;
}

.alloy-refresh-icon-wrapper {
  position: relative;
  width: 40px;
  margin: auto;
  text-align: center;
  height: 25px;
}

.alloy-refresh-icon {
  position: absolute;
  top: 0;
  left: 12px;
  width: 14px;
  height: 25px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(data:image/svg+xml;base64,);
}

.alloy-refresh-icon.rotate {
  transform: translate3d(0, 0, 0) rotate(180deg);
}

.alloy-refresh-icon.loading {
  animation: alloy-loading 0.8s infinite linear;
  background-image: url(data:image/svg+xml;base64,);
}

@keyframes alloy-loading {
  from {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }
  to {
    transform: translate3d(0, 0, 0) rotate(360deg);
  }
}

.alloy-refresh-text {
  color: #333;
  font-size: 12px;
  text-align: center;
}

// 加载更多
.alloy-load-more {
  position: absolute;
  bottom: 20px;
  visibility: hidden;
  height: 20px;
  width: 100%;
  &::after {
    position: absolute;
    height: 2rem;
    width: 2rem;
    margin: 0 auto;
    bottom: 0;
    left: 0;
    right: 0;
    background: transparent url('data:image/svg+xml;charset=utf-8,<svg viewBox=\'0 0 120 120\' xmlns=\'http://www.w3.org/2000/svg\' xmlns:xlink=\'http://www.w3.org/1999/xlink\'><defs><line id=\'l\' x1=\'60\' x2=\'60\' y1=\'7\' y2=\'27\' stroke=\'%236c6c6c\' stroke-width=\'11\' stroke-linecap=\'round\'/></defs><g><use xlink:href=\'%23l\' opacity=\'.27\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(30 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(60 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(90 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(120 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.27\' transform=\'rotate(150 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.37\' transform=\'rotate(180 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.46\' transform=\'rotate(210 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.56\' transform=\'rotate(240 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.66\' transform=\'rotate(270 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.75\' transform=\'rotate(300 60,60)\'/><use xlink:href=\'%23l\' opacity=\'.85\' transform=\'rotate(330 60,60)\'/></g></svg>') center no-repeat;
    background-size: 2rem 2rem;
    content: '';
    text-align: center;
    text-indent: 2rem;
    line-height: 4rem;
    color: #999;
    animation: alloy-load-more 1s step-end infinite;
  }
}

@keyframes alloy-load-more {
  0% {
    transform: rotate(0deg)
  }
  8.33333333% {
    transform: rotate(30deg)
  }
  16.66666667% {
    transform: rotate(60deg)
  }
  25% {
    transform: rotate(90deg)
  }
  33.33333333% {
    transform: rotate(120deg)
  }
  41.66666667% {
    transform: rotate(150deg)
  }
  50% {
    transform: rotate(180deg)
  }
  58.33333333% {
    transform: rotate(210deg)
  }
  66.66666667% {
    transform: rotate(240deg)
  }
  75% {
    transform: rotate(270deg)
  }
  83.33333333% {
    transform: rotate(300deg)
  }
  91.66666667% {
    transform: rotate(330deg)
  }
  100% {
    transform: rotate(360deg)
  }
}